import { useEffect } from "react"
import log from "../../shared/log"
import { useInView } from "react-intersection-observer";
import useContent from "../../shared/useContent";

export default function Main() {

    const content = useContent()

    const { ref, inView } = useInView({ threshold: 0.5 });

    useEffect(() => {
        log("MAIN")
    }, [])

    return (
        <div className="main" ref={ref}>
            <div className={`container ${inView ? "animation-fade" : ""}`}>
                <div className="left">
                    <h1>{content.value.bio.name}</h1>
                    <h3>{content.value.moto[0]}</h3>
                    <button onClick={() => window.open("https://wa.me/+6285156563252", '_blank')}>
                        <img src={require("../../assets/icon/whatsapp.png")} alt="whatsapp" />
                        {content.display.contact_me}
                    </button>
                </div>
                <div className="right">
                </div>
            </div>
        </div>
    )
}