export default function ImagePreview(props: { name: string, frame: string, onClose: any }) {
    return (
        <div className="image-preview">
            <img className="close" src={require("../assets/icon/close.png")} alt="prev" onClick={props.onClose} />
            <img className={props.frame} src={require("../assets/img" + props.name)} alt="prev" />
        </div>
    )
} 