import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { useInView } from "react-intersection-observer";
import useContent from "../../shared/useContent";
import useLanguage from "../../shared/useLanguage";

export default function Portfolio() {

    const [language] = useLanguage()
    const content = useContent()

    const { ref, inView } = useInView({ threshold: 0.5 });

    const navigate = useNavigate()

    const [portoIndex, setPortoIndex] = useState(0)
    const [directionOut, setDirectionOut] = useState<-1 | 0 | 1>(0)
    const [directionIn, setDirectionIn] = useState<-1 | 0 | 1>(0)

    const handlePrev = () => {
        if (portoIndex > 0) {
            setDirectionOut(-1)
            setDirectionIn(0)
            setTimeout(() => {
                setPortoIndex(portoIndex - 1)
                setDirectionOut(0)
                setDirectionIn(-1)
            }, 500)
        }
    }

    const handleNext = () => {
        if (portoIndex < (content.value.portfolio_list.length - 1)) {
            setDirectionOut(1)
            setDirectionIn(0)
            setTimeout(() => {
                setPortoIndex(portoIndex + 1)
                setDirectionOut(0)
                setDirectionIn(1)
            }, 500)
        }
    }

    return (
        <div className="portfolio" ref={ref}>
            <span className="watermark">
                {content.display.portfolio}
            </span>
            <div className={`container ${inView ? "animation-fade" : ""}`}>
                <div key={content.value.portfolio_list[portoIndex].name} className="item">
                    <div className="header">
                        <div className="container">
                            <h2>{content.value.portfolio_list[portoIndex].name}</h2>
                            <h4>{content.value.portfolio_list[portoIndex].skills.map(skill => ` ${skill}`).toString()}</h4>
                        </div>
                    </div>
                    <div className="body">
                        <div className={`left ${portoIndex === 0 ? "disabled" : ""}`} onClick={handlePrev}>
                            <img src={require("../../assets/icon/left-arrow.png")} height={26} width={26} alt="left" />
                        </div>
                        <div className="content">
                            <img
                                className={`
                                    ${content.value.portfolio_list[portoIndex].imageFrame}
                                    ${directionOut === -1 && ` animation-slide-out-right`}
                                    ${directionOut === 1 && ` animation-slide-out-left`}
                                    ${directionIn === -1 && ` animation-slide-in-right`}
                                    ${directionIn === 1 && ` animation-slide-in-left`}
                                `}
                                src={require("../../assets/img/projects" + content.value.portfolio_list[portoIndex].images[0])}
                                alt="lol"
                            />
                        </div>
                        <div className={`right ${portoIndex === content.value.portfolio_list.length - 1 ? "disabled" : ""}`} onClick={handleNext}>
                            <img src={require("../../assets/icon/right-arrow.png")} height={26} width={26} alt="right" />
                        </div>
                    </div>
                    <div className="footer">
                        <button onClick={() => navigate(`/portfolio?index=${portoIndex}&language=${language}`)}>
                            {content.display.detail}
                        </button>
                        <div className="nav">
                            {content.value.portfolio_list.map((p, i) => (
                                <div key={p.name} className={`dot ${i === portoIndex ? "active" : ""}`}></div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}