import useLanguage from "../../shared/useLanguage"

export default function Menu() {

    const [language, setLanguage] = useLanguage()

    return (
        <div className="menu">
            <div className="language-list">
                <button className={`language ${language === "EN" && 'active'}`} onClick={() => setLanguage("EN")}>EN</button>
                <button className={`language ${language === "ID" && 'active'}`} onClick={() => setLanguage("ID")}>ID</button>
            </div>
        </div>
    )
}