import { child, get, ref, set, update } from "firebase/database"
import moment from "moment"
import database from "./database"

const log = (name: string) => {
    interface InterfaceIpLocator {
        ip: string,
        network: string,
        version: string,
        city: string,
        region: string,
        region_code: string,
        country: string,
        country_name: string,
        country_code: string,
        country_code_iso3: string,
        country_capital: string,
        country_tld: string,
        continent_code: string,
        in_eu: boolean,
        postal: number,
        latitude: number,
        longitude: number,
        timezone: string,
        utc_offset: string,
        country_calling_code: string,
        currency: string,
        currency_name: string,
        languages: string,
        country_area: number,
        country_population: number,
        asn: string,
        org: string,
    }
    fetch("https://ipapi.co/json")
        .then(async response => {
            const data: InterfaceIpLocator = await response.json()
            const snapShoot = await get(child(ref(database), "log/" + data.ip.replaceAll('.', '') + "/page_logs"))
            let previousPageLogs: any = []
            snapShoot.forEach((child) => {
                previousPageLogs.push(child.val())
            })
            await set(ref(database, "log/" + data.ip.replaceAll('.', '')), {
                city: data.city,
                country: data.country_name,
                ip: data.ip,
                latitude: data.latitude,
                longitude: data.longitude,
                provider: data.org
            })
            await update(ref(database, "log/" + data.ip.replaceAll('.', '')), {
                page_logs: [
                    ...previousPageLogs,
                    {
                        name: name,
                        timestamp: moment().format("YYYY-MM-DD HH:MM:SS")
                    }
                ]
            })
        })
}

export default log