import value_id from "../assets/content/ID/value.json"
import value_en from "../assets/content/EN/value.json"

import display_id from "../assets/content/ID/display.json"
import display_en from "../assets/content/EN/display.json"

import useLanguage from "./useLanguage"

export default function useContent() {

    const [language] = useLanguage()


    if (language === "EN") {
        return {
            value: value_en,
            display: display_en
        }
    }

    return {
        value: value_id,
        display: display_id
    }

}