import { useSearchParams } from "react-router-dom"
import { useEffect, useState } from "react"
import ImagePreview from "../../shared/ImagePreview"
import log from "../../shared/log"
import useContent from "../../shared/useContent"

export default function PortfolioDetail() {

    const content = useContent()

    const [searchParams] = useSearchParams()

    const [index, setIndex] = useState(0)
    const [imageSelected, setImageSelected] = useState("")

    useEffect(() => {
        setIndex(parseInt(searchParams.get("index") || "0"))
        log("PORTOFOLIO_DETAIL " + content.value.portfolio_list[parseInt(searchParams.get("index") || "0")].id)
    }, [searchParams, content.value.portfolio_list])

    useEffect(() => {
        if (imageSelected !== "") {
            log("PORTOFOLIO_DETAIL " + content.value.portfolio_list[parseInt(searchParams.get("index") || "0")].id + " " + imageSelected)
        }
    }, [content.value.portfolio_list, imageSelected, searchParams])

    return (
        <div className="portfolio-detail">
            {Boolean(imageSelected) && (
                <ImagePreview frame={content.value.portfolio_list[index].imageFrame} name={"/projects" + imageSelected} onClose={() => setImageSelected("")} />
            )}
            <div className="container">
                <h1>{content.value.portfolio_list[index].name}</h1>
                <h3>{content.value.portfolio_list[index].year}</h3>
                <h4>{content.value.portfolio_list[index].skills.map(skill => ` ${skill}`).toString()}</h4>
                <p>{content.value.portfolio_list[index].description}</p>
                <div className="images">
                    {content.value.portfolio_list[index].images.map(img => (
                        <div className="image" key={img} onClick={() => setImageSelected(img)}>
                            <img className={content.value.portfolio_list[index].imageFrame} src={require("../../assets/img/projects" + img)} alt="lol" />
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}